.container{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
.heading{
    margin-top: 1.3rem;
    font-size: 2.5rem;
    font-family: 'Ubuntu', sans-serif;
    color: azure;
}
.search{
    margin-top: 0.7rem;
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    height: 4rem;
    width: 450px;
    margin-left: 4.2rem;
}
.search input{
    height: 2.5rem;
    outline: none;
    border-radius: 10px;
    width: 20rem;
    text-align: center;
    font-size: 1.4rem;
    font-family: 'Ubuntu', sans-serif;
    border: none;
    color: rgb(78, 9, 95);

    background-color: rgb(37, 240, 255);
}

.refresh img{
    width: 2.5rem;
    cursor: pointer;
}
.coin-container{
    
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-evenly;
}

.refresh img:active{
    transform: scale(0.9);
}
@media screen  and (max-width: 480px){

    .heading{
        font-size: 2rem;
    }
    .search{
        margin-top: 0.4rem;
        height: 4rem;
        width: 350px;
        margin-left: 2rem;

    }
    .search input{
        height: 2rem;
        border-radius: 8px;
        width: 15rem;
        font-size: 1.2rem;
        
    }
    .refresh img{
        width: 2rem;
        cursor: pointer;
    }
}